body {
    background-color: rgb(175, 235, 255);
    text-align: center;
    font-family: 'Arial';
}

.banner {
    background-color: rgb(56, 190, 235);
    align-items: center;
    display: flex;
    justify-content: center;
}

.profile-picture-container{
    width: 100px;
    height: 100px;
    overflow: hidden;
    position: relative;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid white;
    
}

.add-margin{
    margin-right: 30%;
}

.profile-picture {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.username {
    color: black;
    margin-left: 30%;
}

.split {
    height: 100%;
    width: 50%;
    position: absolute;
    overflow-x: hidden;
}

.left {
    left: 0;
}

.right {
    right: 0;
    
}

.info {
    position: absolute;
    text-align: left;
    margin-left: 5%;
}

.contact-card {
    margin-left: 70%;
    padding: 10px;
    padding-top: 2px;
    text-align: center;
    max-width:fit-content;
    max-height: fit-content;
    background-color: white;
    border: 2px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.chat-button {

}

.resume-card {
    padding: 10px;
    padding-top: 2px;
    text-align: center;
    max-width:fit-content;
    max-height: fit-content;
    background-color: white;
    border: 2px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}